.grid {
  display: grid;
}

.main-header {
  height: 100vh;
  width: 100%;
  background: url(../img/0-header.jpg) 50% 50% / cover no-repeat;
  background-position: center;
  position: relative;
  &::before {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: @dk;
    opacity: 0.92;
  }
  .menu {
    height: 100px;
    width: 100%;
    padding: 0 @gut;
    // margin-top: 100px;
    z-index: 2;
    display: grid;
    background: @dk;
    position: fixed;
    align-content: center;
    grid:
      "logo nav"
      / 2fr 1fr;
    @media (max-width: 1000px) {
      grid:
        "logo nav"
        / 1fr 1fr;
    }

    .logo {
      grid-area: logo;
      font-size: 30px;
      font-weight: 900;
      color: rgb(255, 255, 255);
    }
    .nav {
      // position: relative;
      grid-area: nav;
      @media (max-width: 1000px) {
        display: flex;
        flex-direction: column;
        // justify-content: flex-end;
        // align-items: flex-end;
      }
      .hamburger {
        display: none;
        z-index: 1;
        align-self: flex-end;
        @media (max-width: 1000px) {
          display: inline-block;
        }
        .hamburger-box {
          .hamburger-inner {
            background-color: rgb(255, 255, 255);
            &::after,
            &::before {
              background-color: rgb(255, 255, 255);
            }
          }
        }
      }
      ul {
        display: flex;
        justify-content: space-between;
        width: 100%;
        background: @dk;
        opacity: .6;
        // top: 200px;
        // position: static;
        // position: absolute;
        @media (max-width: 1000px) {
          display: none;
          position: fixed;
          margin-top: 70px;
        }

        li {
          list-style-type: none;
          @media (max-width: 1000px) {
            text-align: start;
            width: 100%;
            &:hover{
              left: 5px;
            }
          }
          a {
            padding: 10px 20px;
            text-decoration: none;
            color: rgb(255, 255, 255);
            font-size: 25px;
            &:hover {
              color: rgb(101, 146, 204);
            }
          }
        }
      }
    }
  }
  .header-content {
    z-index: 1;
    color: rgb(255, 255, 255);
    text-align: center;
    align-content: center;
    // margin: 200px @gut;
    // position: absolute;
    h1 {
      // font-size: 60px;
      font-size: 3.75em;
      // font-size: 300%;
      text-transform: uppercase;
      font-family: "intro-grid", sans-serif;
      font-weight: 800;
      margin: 20px 0;
      line-height: 1.5;
      // @media (max-width: 768px) {
      //       font-size: 25px
      // }
    }

    @media (max-width: 390px) {
      h1 {
        font-size: 3em;
      }
    }
    @media (max-width: 888px) {
      h1 {
        font-size: 3em;
        padding: 0 20px;
      }
    }
    .header-buttons {
      .button {
        // font-family: "intro-grid", sans-serif;
        margin: 10px;
        display: inline-block;
        border: none;
        color: #fff;
        text-decoration: none;
        background-color: @or;
        padding: 10px 70px;
        font-size: 18px;
        text-transform: capitalize;
        font-weight: 900;
        // letter-spacing: 3px;
        border-radius: 2px;
        text-align: center;
        position: relative;
        outline: none;
        transition: background-color 0.1s ease;
        cursor: pointer;
        &::after {
          transition: background-color 0.2s ease;
          position: absolute;
          content: "";
          height: 4px;
          bottom: 0;
          // top: 0;
          width: 100%;
          background-color: darken(@or, 50%);
          opacity: 0.18;
          border-bottom-left-radius: 2px;
          border-bottom-right-radius: 2px;
          left: 0;
        }
        &:focus,
        &:hover {
          text-decoration: none;
          color: #fff;
        }
        &:hover {
          background-color: lighten(@or, 5%);
          &::after {
            opacity: 0.22;
          }
        }
        &:active {
          background-color: darken(@or, 5%);
          &::after {
            opacity: 0.32;
          }
        }
        &.button-grey {
          background-color: @gr;
          &::after {
            background-color: darken(@gr, 15%);
            // opacity: 0.32;
          }
          &:hover {
            background-color: lighten(@gr, 5%);
            &::after {
              opacity: 0.22;
            }
          }
          &:active {
            background-color: darken(@gr, 5%);
            &::after {
              opacity: 0.32;
            }
          }
        }
      }

      a {
        text-decoration: none;
        color: rgb(224, 134, 134);
      }
    }
  }
}
.portfolio-section {
  padding: 10px;
  // grid-template-columns: repeat(4, 1fr);
  grid-gap: 10px;

  div:nth-of-type(1) {
    grid-area: img-1;
  }
  div:nth-of-type(2) {
    grid-area: img-2;
  }
  div:nth-of-type(3) {
    grid-area: img-3;
  }
  div:nth-of-type(5) {
    grid-area: img-5;
  }
  div:nth-of-type(5) {
    grid-area: img-5;
  }
  div:nth-of-type(6) {
    grid-area: img-6;
  }
  div:nth-of-type(7) {
    grid-area: img-7;
  }
  div:nth-of-type(8) {
    grid-area: img-8;
  }
  div:nth-of-type(9) {
    grid-area: img-9;
  }

  .buttoncase {
    grid-area: button;
  }

  grid:
    "img-1 img-1 img-2 img-3"
    "img-4 img-5 img-5 img-6"
    "img-7 img-8 img-9 img-6"
    " .   button button  .";

  @media (max-width: 1200px) {
    grid:
      "img-1 img-1 img-4"
      "img-3 img-2 img-2"
      "img-5 img-5 img-6"
      "img-7 img-8 img-8"
      "img-9 img-9 img-9"
      " .   button   .";
  }
  @media (max-width: 768px) {
    grid:
      "img-1   img-2"
      "img-3   img-4 "
      "img-5   img-6"
      "img-7   img-8"
      "img-9   img-9"
      "button button";
  }
  @media (max-width: 576px) {
    grid:
      "img-1"
      "img-2"
      "img-3"
      "img-4"
      "img-5"
      "img-6"
      "img-7"
      "img-8"
      "img-9"
      "button";
  }

  @media (max-width: 390px) {
    h1 {
      font-size: 5em;
    }
  }

  .portfolio-item {
    min-height: 32vh;
    background-size: cover;
    background-position: center;
    justify-content: center;
    text-align: center;
    align-items: center;
    color: rgb(255, 255, 255);
    font-size: 20px;
    font-weight: bold;
    padding: @gut;
    position: relative;
    transition: color 0.5s ease;
    cursor: pointer;
    &:hover {
      color: @or;
      &::before {
        opacity: 0.85;
        transition: opacity 0.5s;
      }
    }
    &::before {
      content: "";
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background-color: @dk;
      opacity: 0.65;
      transition: opacity 0.5s ease;
    }
    * {
      z-index: 1;
    }
  }
  .buttoncase {
    margin: @gut 0 50px;
    .button {
      // font-family: "intro-grid", sans-serif;
      margin: 10px;
      display: inline-block;
      border: none;
      color: #fff;
      text-decoration: none;
      background-color: @or;
      padding: 10px 70px;
      font-size: 18px;
      text-transform: capitalize;
      font-weight: 900;
      // letter-spacing: 3px;
      border-radius: 2px;
      text-align: center;
      position: relative;
      outline: none;
      transition: background-color 0.1s ease;
      cursor: pointer;
      &::after {
        transition: background-color 0.2s ease;
        position: absolute;
        content: "";
        height: 4px;
        bottom: 0;
        // top: 0;
        width: 100%;
        background-color: darken(@or, 50%);
        opacity: 0.18;
        border-bottom-left-radius: 2px;
        border-bottom-right-radius: 2px;
        left: 0;
      }
      &:focus,
      &:hover {
        text-decoration: none;
        color: #fff;
      }
      &:hover {
        background-color: lighten(@or, 5%);
        &::after {
          opacity: 0.22;
        }
      }
      &:active {
        background-color: darken(@or, 5%);
        &::after {
          opacity: 0.32;
        }
      }
      &.button-grey {
        background-color: @gr;
        &::after {
          background-color: darken(@gr, 15%);
          // opacity: 0.32;
        }
        &:hover {
          background-color: lighten(@gr, 5%);
          &::after {
            opacity: 0.22;
          }
        }
        &:active {
          background-color: darken(@gr, 5%);
          &::after {
            opacity: 0.32;
          }
        }
      }
    }
  }
}

.section-ready {
  padding: 100px 0;
  background-color: @li;

  .container {
    width: 100%;
    display: grid;
    padding: 0 10vw;
    grid-template-columns: repeat(2, 1fr);

    @media (max-width: 1215px) {
      grid-template-columns: 1fr;
      .ready-right {
        display: none;
      }
      h2 {
        text-align: center;
      }
      .ready-left {
        padding: 0;
        border-right: none;
      }
    }

    .ready-left {
      border-right: 2px rgb(218, 218, 218) solid;

      h2 {
        font-size: 34px;
        font-weight: 800;
        color: @dk;
        line-height: 1.2;
      }

      p {
        font-size: 18px;
        margin: 60px 0 0;
        font-weight: 800;
        color: lighten(@gr, 20%);
      }
    }

    .ready-right {
      background: url(../img/example.jpg) 50% 30% / cover no-repeat;
      padding: 0 0 0 40px;
    }
  }
}

.form-section {
