    padding: 0 10vw;
    background-color: #f7f7f7;
    h2 {
      font-size: 34px;
      font-weight: 800;
      color: @dk;
      line-height: 1.2;
      margin: 0 0 40px 0;
    }
    .blog-home {
      width: 100%;
      display: grid;
      grid-gap: @gut;
      grid:
        "pic-1 pic-2 pic-3 pic-3"
        "pic-4 pic-4 pic-5 pic-6";
      text-align: center;

      @media (max-width: 1540px) {
        grid:
          "pic-1 pic-2"
          "pic-3 pic-3"
          "pic-4 pic-5"
          "pic-6 pic-6";
