  font-weight: 800;
  margin: 20px 0;
  line-height: 1.5;
}

.main-header .header-content .header-buttons .button {
  margin: 10px;
  display: inline-block;
  border: none;
  color: #fff;
  text-decoration: none;
  background-color: #f0650d;
  padding: 10px 70px;
  font-size: 18px;
  text-transform: capitalize;
  font-weight: 900;
  -webkit-border-radius: 2px;
  border-radius: 2px;
  text-align: center;
  position: relative;
  outline: none;
  -webkit-transition: background-color 0.1s ease;
  -o-transition: background-color 0.1s ease;
  transition: background-color 0.1s ease;
  cursor: pointer;
}

.main-header .header-content .header-buttons .button::after {
  -webkit-transition: background-color 0.2s ease;
  -o-transition: background-color 0.2s ease;
  transition: background-color 0.2s ease;
  position: absolute;
  content: "";
  height: 4px;
  bottom: 0;
  width: 100%;
  background-color: #000000;
  opacity: 0.18;
  -webkit-border-bottom-left-radius: 2px;
  border-bottom-left-radius: 2px;
  -webkit-border-bottom-right-radius: 2px;
  border-bottom-right-radius: 2px;
  left: 0;
}

.main-header .header-content .header-buttons .button:focus,
.main-header .header-content .header-buttons .button:hover {
  text-decoration: none;
  color: #fff;
}

.main-header .header-content .header-buttons .button:hover {
  background-color: #f37423;
}

.main-header .header-content .header-buttons .button:hover::after {
  opacity: 0.22;
}

.main-header .header-content .header-buttons .button:active {
  background-color: #d85b0c;
}

.main-header .header-content .header-buttons .button:active::after {
  opacity: 0.32;
}

.main-header .header-content .header-buttons .button.button-grey {
  background-color: #4b5a65;
}

.main-header .header-content .header-buttons .button.button-grey::after {
  background-color: #2a3339;
}

.main-header .header-content .header-buttons .button.button-grey:hover {
  background-color: #566774;
}

.main-header .header-content .header-buttons .button.button-grey:hover::after {
  opacity: 0.22;
}

.main-header .header-content .header-buttons .button.button-grey:active {
  background-color: #404d56;
}

.main-header .header-content .header-buttons .button.button-grey:active::after {
  opacity: 0.32;
}

.main-header .header-content .header-buttons a {
  text-decoration: none;
  color: #e08686;
