    grid: "img-1   img-2" "img-3   img-4 " "img-5   img-6" "img-7   img-8" "img-9   img-9" "button button";
  }
}

@media (max-width: 600px) {
  .landing-section .container .blog-home {
    grid: "pic-1" "pic-2" "pic-3" "pic-4" "pic-5" "pic-6";
  }
}

@media (max-width: 576px) {
  .portfolio-section {
    grid: "img-1" "img-2" "img-3" "img-4" "img-5" "img-6" "img-7" "img-8" "img-9" "button";
  }
}

@media (max-width: 390px) {
  .main-header .header-content h1 {
    font-size: 3em;
  }

  .portfolio-section h1 {
    font-size: 5em;
  }
}